import $C from './config.js'

export default {
  common: {
    method: 'GET',
    header: {
      'content-type': 'application/json',
    },
    data: {}
  },
  request(options = {}) {
    options.url = $C.webUrl + options.url
    options.method = options.method || this.common.method
    options.header = options.header || this.common.header
    try {
      options.header['Authorization'] = uni.getStorageSync('token')
    } catch (e) {
      console.log(e)
    }
    // token
    console.log(options.url)
    // ,,,!!!!
    return uni.request(options)
  },
  async get(url, data = {}, options = {}) {
    options.url = url
    options.data = data
    options.method = 'GET'
    options.header = options.header || this.common.header
    try {
      options.header['Authorization'] = uni.getStorageSync('token')
    } catch (e) {
      console.log(e)
    }
    return new Promise(async (resolve, reject) => {
      const responses = await this.request(options)
      console.log('res ------------', responses);
      if (responses[0]) {
        uni.showToast({ title: '', icon: 'none' })
        reject({ message: "" });
      } else {
        const response = responses[1].data; // data.datareturn res,data
        if (response.code === 401 || response.code === 403) {
          uni.setStorageSync('accountType', '')
          uni.setStorageSync('token', '')
          uni.setStorageSync('phone', '')
          uni.setStorageSync('userId', '')

          uni.showToast({ title: '', icon: 'none' })
          uni.navigateTo({ url: '/pages/login/index' })
          reject({ message: "" });
        }

        if (response.ok && response.code === 200) {
          resolve(response);
        }
        reject(response)
      }
    })
  },
  async post(url, data = {}, options = {}) {
    options.url = url
    options.data = data
    options.method = 'POST'
    options.header = options.header || this.common.header
    try {
      options.header['Authorization'] = uni.getStorageSync('token')
    } catch (e) {
      console.log(e)
    }
    return new Promise(async (resolve, reject) => {
      const responses = await this.request(options)
      console.log('res ------------', responses);
      if (responses[0]) {
        uni.showToast({ title: '', icon: 'none' })
        reject({ message: "" });
      } else {
        const response = responses[1].data; // data.datareturn res,data
        console.log('response', response);
        if (response.code === 401 || response.code === 403) {
          uni.setStorageSync('accountType', '')
          uni.setStorageSync('token', '')
          uni.setStorageSync('phone', '')
          uni.setStorageSync('userId', '')

          uni.showToast({ title: '', icon: 'none' })
          uni.navigateTo({ url: '/pages/login/index' })
          reject({ message: "" });
        }

        if (response.ok && response.code === 200) {
          resolve(response);
        }
        reject(response)
      }
    })
  },

}
